@import "../../vars.scss";

.formMain {
  max-width: 1354px;
  margin: 100px auto 0;
  border-radius: 100px;
  box-shadow: 0 3px 30px rgba(0,0,0,0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 80px 100px;
  &__article {
    font-size: 25px;
    font-weight: bold;
    color: #ffa20e;
  }
  &__form {
    margin-top: 100px;
    width: 100%;
    &-inner {
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
    &-input {
      width: 45%;
      border: none;
      font-weight: 500;
      padding: 20px 0;
      border-bottom: 2px solid #dbdbdb;
      margin-bottom: 80px;
      font-size: 34px;
      &-date {
        color: #b0b0b0;
        &:focus {
          color: black;
        }
      }
      &::placeholder {
        color: #b0b0b0;
      }
    }
  }
  &__checkbox {
    display: flex;
    align-items: center;
    &-input {
      width: 45px;
      height: 45px;
    }
    &-label {
      font-size: 25px;
      color: #afafaf;
      margin-left: 30px;
    }
    &-link {
      color: #ffa20e;
      @include  transitionWebKitDuration;
      &:hover {
        color: #ffca38;
      }
    }
  }
  &__button {
    margin: 0 auto;
    margin-top: 40px;
    display: block;
    width: 425px;
    height: 88px;
    border-radius: 40px;
    background-color: #0dd542;
    border: 1px solid #0dd542;
    text-align: center;
    line-height: 88px;
    color: white;
    font-size: 25px;
    font-weight: bold;
    @include  transitionWebKitDuration;
    &:hover {
      color: #0dd542;
      background-color: white;
    }
  }
}

.disable-button {
  cursor: pointer;
  background-color: #dbdbdb;
  border: 1px solid #dbdbdb;
  &:hover {
    color: white;
    background-color: #dbdbdb;
  }
}

.status-bar {
  position: relative;
  margin-top: 80px;
  width: 100%;
  &__line {
    width: 100%;
    height: 8px;
    border-radius: 4px;
    background-color: #dbdbdb;
    &-inner {
      position: absolute;
      height: 8px;
      border-radius: 4px;
      background-color: #0dd542;
      @include  transitionWebKitDuration;
    }
    &-circle {
      position: absolute;
      right: -10px;
      top: -5px;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-color: #0dd542;
    }
  }
}

input[type="date"]:before {
  color: #b0b0b0;
  content: attr(placeholder) !important;
  margin-right: 0.5em;
}

input[type="date"]:focus:before {
  content: '' !important;
}

